.header {
  background-color: rgba(25, 34, 33, .62);
  padding: 10px;
}

.form {
  max-width: 310px;
  gap: 5px;
  margin: 0 auto;
  display: flex;
}

.form__input {
  width: 100%;
  padding: 5px 5px 5px 20px;
}

.form__btn {
  flex-grow: 1;
}

.input__wrapper {
  position: relative;
}

.icon {
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: #000;
  fill: #000;
  z-index: 10;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 3px;
  transform: translateY(-50%);
}

.go-back {
  position: absolute;
  bottom: 0;
}

.main {
  padding-top: 10px;
  padding-bottom: 10px;
}

.list {
  gap: 10px;
  display: grid;
}

@media (min-width: 425px) {
  .list {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 768px) {
  .list {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1280px) {
  .list {
    grid-template-columns: repeat(5, 1fr);
  }
}

.list__item img {
  aspect-ratio: 2 / 2.2;
  width: 100%;
  object-fit: cover;
  object-position: center;
}

.load-more {
  background-color: rgba(80, 84, 95, .616);
  border-radius: .9375rem;
  margin: 10px auto 0;
  padding: 5px 10px;
  display: block;
}

.is-hidden {
  display: none;
}

.footer {
  background-color: rgba(25, 34, 33, .62);
  padding: 10px;
}

.footer b {
  color: #fff;
}

.loader__wrapper {
  z-index: 10;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader {
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  background-color: rgba(180, 159, 159, .235);
  border: 10px solid #bd650d;
  border-color: #bd650d rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation .8s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

